var _self;
export default {
	data() {
		return {
			source: 0,
			addressList: [
				{
					address_id: 1,
					name: '刘晓晓',
					mobile: '18666666666',
					addressName: '贵族皇仕牛排(东城店)',
					address: '北京市东城区',
					area: 'B区',
					default: true
				},{
					address_id: 2,
					name: '刘大大',
					mobile: '18667766666',
					addressName: '龙回1区12号楼',
					address: '山东省济南市历城区',
					area: '西单元302',
					default: false,
				}
			]
		}
	},
	onLoad(option){
		_self =this;
		// console.log(option.source);
		this.source = option.source;
	},
	onShow() {
		_self.loadAddress();			
	},
	methods: {
		//加载全部地址列表、
		async loadAddress(){
				let addrList = await _self.$apiRequest({
					url:'addresses'
				});
				//console.log(addrList);
				_self.addressList  = addrList.data.data;
				
		},
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			//this.addressList.unshift(data);
			//_self.loadAddress();
			//console.log(data, type);
		}
	}
}
